  dialog.full, .reveal-modal.full {
    top: 0;
    left: 0;
    height: 100vh;
    min-height: 100vh;
    margin-left: 0 !important; }
    @media only screen and (min-width:40.063em) {
      dialog.full, .reveal-modal.full {
        margin-left: -50vw;
        width: 100vw; } } }

@media print {
  dialog, .reveal-modal {
