/* Styles for Step By Step */
/* Cirlce Styles */
.circlenumber {
  background-color: none;
  border-radius: 50%;
  border: 1px solid #0b4f69;
  position: relative; }
  .circlenumber h3 {
    line-height: 1px;
    color: #0b4f69;
    position: relative;
    text-align: center;
    font-weight: normal; }

/* Circle Style One Mobile */
@media only screen and (min-width: 0) and (max-width: 40em) {
  .step-by-step-style-one .circlenumber {
    width: 80px;
    height: 80px;
    margin: 30px auto 15px auto; }
    .step-by-step-style-one .circlenumber h3 {
      padding-top: 35px;
      font-size: 20px; } }

/* Circle Style One Medium and Large */
@media only screen and (min-width: 40em) {
  .step-by-step-style-one .path-item {
    padding-left: 55px;
    position: relative; }
    .step-by-step-style-one .path-item .circlenumber {
      width: 40px;
      height: 40px;
      position: absolute;
      margin-top: 6px;
      left: 0; }
      .step-by-step-style-one .path-item .circlenumber h3 {
        padding-top: 15px;
        font-size: 16px; } }

